import React, { useEffect, useState } from "react";
import './sorts.scss';
import { SORTS_DATA, STYLES_LIST } from '../../service/consts';
import { Style } from "../../service/types";

const Sorts = () => {
    const [styleFilter, setStyleFilter] = useState<Style[]>([]);
    const [sorts, setSorts] = useState(SORTS_DATA);
    const [filtersShown, setFiltersShown] = useState(false);

    useEffect(() => {
        if (!styleFilter.length) {
            setSorts(SORTS_DATA);
        } else {
            setSorts(SORTS_DATA.filter(sort => sort.style && styleFilter.includes(sort.style)));
        }
    }, [styleFilter])

    return (
        <div className="sorts">
            <div className="container">
                <div className="inner-p">
                    <div className="sorts__content">
                        <div className={`filters ${filtersShown ? 'filters-shown' : ''}`}>
                            <img
                                className="filters-close"
                                src={require(`../../images/icons/close.svg`).default}
                                alt={''}
                                onClick={() => setFiltersShown(false)}
                            />
                            <div className="filters__item">
                                <h3 className="title">Стиль</h3>
                                <div className="list">
                                    {
                                        STYLES_LIST.map(style => (
                                            <div className="checkbox-block" key={style} 
                                                onClick={(e) => {
                                                    if (!styleFilter.includes(style)) {
                                                        setStyleFilter([...styleFilter, style]);
                                                        return;
                                                    }
                                                    const newFilter = styleFilter.filter(val => val !== style);
                                                    setStyleFilter([...newFilter]);
                                                }}>
                                                <input
                                                    checked={styleFilter.includes(style)}
                                                    type="checkbox" className="checkbox"
                                                    name={style}
                                                    onChange={() => {}}
                                                />
                                                <label htmlFor={style}>{style.toUpperCase()}</label>
                                            </div>
                                        ))
                                    }
                                </div>
                            </div>
                        </div>

                        <div className="sorts-list">
                            <div className="title">
                                <h2 className="h1">Сорта</h2>
                                <img
                                    className="filter-btn"
                                    src={require(`../../images/icons/filter.svg`).default}
                                    alt=''
                                    onClick={() => setFiltersShown(!filtersShown)}
                                />
                            </div>
                            <div className="list">
                                {sorts.map(sort => (
                                    <div className="list__item" key={sort.id}>
                                        <div className="title">
                                            <h3>{sort.title}</h3>
                                            <span className="desc">{sort.taste}</span>
                                        </div>

                                        <img className="beer-image" src={require(`../../images/sorts/${sort.image}`)} alt=""/>
                                        <img className="style-cover" src={require(`../../images/sort-styles/${sort.style}.svg`)} alt=''/>

                                        <div className="description">
                                            {Object.entries(sort.props).map(([key, val]) => (
                                                <span key={`${key}: ${val}`}>{key}: {val}</span>
                                            ))}
                                            <p className="desc">{sort.description}</p>
                                        </div>

                                    </div>
                                ))}
                            </div>
                        </div>
                    </div>

                </div>
            </div>
			{filtersShown && <div className={`cover cover-shown`} onClick={() => setFiltersShown(false)}/>}
            <img className='bg-beer' src={require('../../images/bg-beer.svg').default} alt=''/>
        </div>
    )
};

export default Sorts